.markdown-body {
  --post-theme: light;
  --post-color-scheme: light;
  --post-color-primary: #0e5efe;
  --post-color-primary-bg: #e2f3ff;
  --post-color-primary-text: #0e5efe;
  --post-color-text: #1f1f1f;
  --post-color-text-secondary: #8c8c8c;
  --post-color-on-primary: #fff;
  --post-color-on-primary-bg: #2b36df;
  --post-color-bg: transparent;
  --post-font-size-base: 14px;
  --post-font-size-h1: 2.25em;
  --post-font-size-h2: 1.75em;
  --post-font-size-h3: 1.5em;
  --post-font-size-h4: 1.125em;
  --post-font-size-h5: .875em;
  --post-font-size-h6: 1em;
  --post-font-family: -apple-system, blinkmacsystemfont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, helvetica, arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  --post-font-family-code: menlo, monaco, consolas, ubuntu mono;
  --post-codeblock-bg: #141414;
  --post-table-bg: rgba(226, 243, 255, .2);
  --post-blockquote-text-color: #595959;
  --post-blockquote-symbol-bg: #f0f0f0;
  --post-blockquote-symbol-content: "";
  --post-blockquote-symbol-size: 40px;
  --post-hr-height: 4px;
  --post-hr-bg: #f0f0f0;
  --post-p-font-size: 1.125em;
  --post-codeblock-text-color: #fff;
  --post-codeblock-font-family: menlo, monaco, consolas, ubuntu mono;
  --post-codeblock-font-size: 1em;
  --post-table-border-color: #d9d9d9;
  --post-blockquote-symbol-color: rgba(140, 140, 140, .5);
  --post-list-bg-color: #e2f3ff;
  --post-link-text-color: #0e5efe;
  --post-link-code-text-color: #0e5efe;
  --post-code-text-color: #2b36df;
  --post-code-bg-color: #e2f3ff;
  --post-highlight-text-color: #fff;
  color-scheme: var(--post-color-scheme);
  -webkit-text-size-adjust: 100%;
  font-family: var(--post-font-family);
  font-size: var(--post-font-size-base);
  color: var(--post-color-text);
  background: var(--post-color-bg);
}

.markdown-body blockquote {
  padding: 0 20px 0 var(--post-blockquote-symbol-size);
  color: var(--post-blockquote-text-color);
  margin: 2.25em 0;
  position: relative;
}

.markdown-body blockquote:before {
  content: var(--post-blockquote-symbol-content);
  min-width: 8px;
  text-align: center;
  color: var(--post-blockquote-symbol-color);
  background: var(--post-blockquote-symbol-bg);
  font-family: Helvetica Neue, Arial, Times New Roman, Impact, sans-serif;
  font-size: 5em;
  font-weight: bold;
  line-height: .6em;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.markdown-body blockquote > :last-child {
  margin-bottom: 0;
}

.markdown-body code {
  background: var(--post-code-bg-color);
  color: var(--post-code-text-color);
  border-radius: 3px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 2px 5px;
  font-family: inherit;
}

.markdown-body pre {
  background: var(--post-codeblock-bg);
  border-radius: 6px;
  margin-top: 0;
  margin-bottom: 25px;
  padding: 16px 20px;
  overflow: auto;
}

.markdown-body pre > code {
  line-height: 1.5;
  font-size: var(--post-codeblock-font-size);
  color: var(--post-codeblock-text-color);
  font-family: var(--post-codeblock-font-family);
  background: none;
  margin: 0;
  padding: 0;
}

.markdown-body h1 {
  font-size: var(--post-font-size-h1);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h1:first-child {
  margin-top: 0;
}

.markdown-body h2 {
  font-size: var(--post-font-size-h2);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h2:first-child {
  margin-top: 0;
}

.markdown-body h3 {
  font-size: var(--post-font-size-h3);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h3:first-child {
  margin-top: 0;
}

.markdown-body h4 {
  font-size: var(--post-font-size-h4);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h4:first-child {
  margin-top: 0;
}

.markdown-body h5 {
  font-size: var(--post-font-size-h5);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h5:first-child {
  margin-top: 0;
}

.markdown-body h6 {
  font-size: var(--post-font-size-h6);
  margin-top: .9375em;
  margin-bottom: .9375em;
  font-weight: 600;
  line-height: 1.5;
}

.markdown-body h6:first-child {
  margin-top: 0;
}

.markdown-body hr {
  height: var(--post-hr-height);
  background: var(--post-hr-bg);
  border: 0;
  margin: 1.6em 0;
}

.markdown-body img {
  max-width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
}

.markdown-body a {
  color: var(--post-link-text-color);
  text-decoration: none;
}

.markdown-body a code {
  color: var(--post-link-code-text-color);
}

.markdown-body a:active {
  opacity: .7;
}

.markdown-body a:hover {
  text-decoration: underline;
}

.markdown-body ol, .markdown-body ul {
  margin-top: 0;
  margin-bottom: 1.5em;
  padding-left: 1em;
}

.markdown-body ol ol, .markdown-body ol ul, .markdown-body ul ol, .markdown-body ul ul {
  margin-bottom: 0;
  padding-left: 1.75em;
}

.markdown-body ol li, .markdown-body ul li {
  margin-top: 5px;
  font-size: 1em;
  line-height: 1.6;
}

.markdown-body ol li input[type="checkbox"], .markdown-body ul li input[type="checkbox"] {
  width: 1.1em;
  height: 1.1em;
  vertical-align: middle;
  margin-right: .2em;
}

.markdown-body p {
  font-size: var(--post-p-font-size);
  margin-top: 0;
  margin-bottom: 25px;
  line-height: 1.7;
}

.markdown-body table {
  border-spacing: 0;
  border-collapse: collapse;
  margin-top: 0;
  margin-bottom: 25px;
  display: block;
  overflow: auto;
}

.markdown-body table th {
  border: 1px solid var(--post-table-border-color);
  padding: 10px 20px;
  font-size: 1.125em;
  font-weight: 600;
}

.markdown-body table td {
  border: 1px solid var(--post-table-border-color);
  padding: 10px 20px;
  font-size: 1em;
}

.markdown-body table tr:nth-of-type(2n) {
  background: var(--post-table-bg);
}

.markdown-body .highlight {
  border-radius: 6px;
  margin: 0 0 25px;
  overflow: auto;
}

.markdown-body .highlight table {
  max-width: 100%;
  width: 100%;
  margin-bottom: 0;
  display: table;
}

.markdown-body .highlight .code {
  width: 100%;
  padding: 0;
}

.markdown-body .highlight td {
  border: none;
}

.markdown-body .highlight pre {
  color: var(--post-highlight-text-color);
  line-height: 1.5;
  font-family: var(--post-codeblock-font-family);
  border-radius: 0;
  margin-bottom: 0;
}

